import Vue from 'vue'
import '@/utils/setHtmlFontSize'
import lodash from 'lodash'
import { apis } from './request.js'
import * as vantCusMethod from './vant-custom'
import X2js from 'x2js' // xml数据处理插件

const x2js = new X2js()

const allUtils = Object.assign({}, apis, vantCusMethod, { lodash, x2js })

Object.keys(allUtils).forEach(k => {
  Vue.prototype['$' + k] = allUtils[k]
})

export default allUtils



