let WUMASHAREURL = 'WUMASHAREURL'

export function getShareUrl () {
  return window.localStorage.getItem(WUMASHAREURL)
}

export function setShareUrl (shareUrl) {
  window.localStorage.setItem(WUMASHAREURL, shareUrl)
  return true
}

export function removeShareUrl () {
  window.localStorage.removeItem(WUMASHAREURL)
}



