import WeChatAuth from './wechat-auth'
import url from 'url'
import querystring from 'querystring'
import { getToken } from '@/utils/auth'
import gloalParam from '@/globalParam.js'
const wihteRouter = ['/joinWuMa']
export default {
  install (Vue, options) {
    let weChatAuth = new WeChatAuth(options)
    let router = options.router
    if (!router) return false

    function urlCodeQueryFilter (code) {
      if (code) {
        weChatAuth.setAuthCode(code)
        weChatAuth.removeUrlCodeQuery()
        return true
      }
      return false
    }

    function checkRouterAuth (to, from, next) {
      let authCode = weChatAuth.getAuthCode()
      // if ((!to.meta || !to.meta.auth) && !authCode) return true
      // weChatAuth.removeAccessToken()
      if (!authCode) {
        let octothorpe = gloalParam.routerModel === 'hash' ? '#' : ''
        weChatAuth.openAuthPage(encodeURIComponent(window.location.origin + octothorpe + to.fullPath))
        return false
      } else if (wihteRouter.indexOf(to.path) < 0 && !getToken()) {
        weChatAuth.getCodeCallback(next)
        return false
      } else if (wihteRouter.indexOf(to.path) >= 0 && !weChatAuth.getAccessToken()) {
        weChatAuth.getCodeCallback(next)
        return false
      }
      return true
    }

    function beforeEach (to, from, next) {
      let query = querystring.parse(url.parse(window.location.href).query)
      let code = query.code
      let codeFilterRes = urlCodeQueryFilter(code)
      if (codeFilterRes) {
        return false
      }
      if (!code && !checkRouterAuth(to, from, next)) {
        return false
      } else {
        next()
      }
    }

    router.beforeEach((to, from, next) => {
      if (to.path !== location.pathname) {
        // location.assign(to.fullPath)
        next()
      } else if (weChatAuth.config.open) {
        beforeEach(to, from, next)
      } else {
        next()
      }
    })
  }
}



