import Vue from 'vue'
import VueI18n from 'vue-i18n'
// import Cookies from 'js-cookie'

Vue.use(VueI18n)

const i18n = new VueI18n({
  locale: 'zh_CN', // set locale
  messages: {
    zh_CN: require('./zh_CN'),
    en: require('./en'),
    zh_TW: require('./zh_TW')
  }
})

export default i18n



