import axios from 'axios'
import router from '@/router'
import globalParam from '@/globalParam.js'
import { getToken, removeToken } from '@/utils/auth'
import { ToastInfo } from '@/utils/vant-custom/index'

axios.defaults.withCredentials = false
// 创建axios实例
const service = axios.create({
  // baseURL: 'https://www.easy-mock.com/mock/5be97382d04fbb4e5d598ead/example', // process.env.BASE_API, // api的base_url
  timeout: 15000,
  withCredentials: false
})

// request拦截器
service.interceptors.request.use(
  config => {
    var localToken = getToken()
    if (localToken) {
      config.headers.MBPTOKEN = localToken
    }
    config.url = dealUrl(config)
    config.headers['Content-Type'] = 'application/json'
    return config
  },
  error => {
    console.log(error) // for debug
    Promise.reject(error)
  }
)

function dealUrl (config) {
  let url = config.url
  let res = url
  if (url.indexOf('timestamp') < 0 && config.method === 'get') {
    let timestamp = new Date().getTime()
    if (url.indexOf('?') >= 0) {
      res = res + '&timestamp=' + timestamp
    } else {
      res = res + '?timestamp=' + timestamp
    }
  }
  return res
}

// respone拦截器
service.interceptors.response.use(
  response => {
    const resCode = response.data.code
    if (resCode === '0') {
      return response
    } else if (resCode === '-3') {
      if (globalParam.isOpenWxAuth) {
        removeToken()
        router.go(0)
      } else {
        ToastInfo('请重新登陆')
        return response
      }
    } else {
      return response
    }
  },
  error => {
    console.log(error) // for debug
    return Promise.reject(error)
  }
)

const get = (url, params = {}) => {
  return new Promise((resolve, reject) => {
    service.get(url, {
      params: params
    })
      .then(response => {
        console.info(url)
        resolve(response.data)
      })
      .catch(err => {
        reject(err)
      })
  })
}

const post = (url, data = {}) => {
  return new Promise((resolve, reject) => {
    service.post(url, data)
      .then(response => {
        resolve(response.data)
      }, err => {
        reject(err)
      })
  })
}

const drop = (url, data = {}) => {
  return new Promise((resolve, reject) => {
    service.delete(url, data)
      .then(response => {
        resolve(response.data)
      }, err => {
        reject(err)
      })
  })
}

const patch = (url, data = {}) => {
  return new Promise((resolve, reject) => {
    service.patch(url, data)
      .then(response => {
        resolve(response.data)
      }, err => {
        reject(err)
      })
  })
}

const put = (url, data = {}) => {
  return new Promise((resolve, reject) => {
    service.put(url, data)
      .then(response => {
        resolve(response.data)
      }, err => {
        reject(err)
      })
  })
}

export const apis = { get, post, drop, patch, put }

export default service



