import Vue from 'vue'
import router from '@/router'
import WechatAuth from '@/plugins/weChatAuth'
import globalParam from '@/globalParam.js'
import { apis } from '@/utils/request'
import apiUrl from '@/api/index'
import { setToken } from '@/utils/auth'
import { setShareUrl } from '@/utils/wxShareUrl'
// import { ToastInfo } from '@/utils/vant-custom/index'
Vue.use(WechatAuth, {
  open: globalParam.isOpenWxAuth,
  router, // 路由实例对象
  appid: globalParam.wxAppId, // 您的微信appid
  responseType: 'code', // 返回类型，请填写code
  scope: 'snsapi_userinfo', // 应用授权作用域，snsapi_base （不弹出授权页面，直接跳转，只能获取用户openid），snsapi_userinfo （弹出授权页面，可通过openid拿到昵称、性别、所在地。并且，即使在未关注的情况下，只要用户授权，也能获取其信息）
  getCodeCallback (code, next) {
    // 用户同意授权后回调方法
    // code：用户同意授权后，获得code值
    // code说明： code作为换取access_token的票据，每次用户授权带上的code将不一样，code只能使用一次，5分钟未被使用自动过期。
    // next： 无论access_token是否获取成功,一定要调用该方法
    // next说明：next方法接收两个参数
    // 参数1为通过code值请求后端获取到的access_token值，如果获取失败请填入空字符串''
    // 参数2(非必填，默认获取access_token切换到当前路由对象)，指定切换对象 next('/') 或者 next({ path: '/' })
    apis.get(apiUrl.module.memberWxLogin, {
      code: code
    }).then(response => {
      if (response.code === '-7') {
        isShareUrl()
        next(true, response.data, '/joinWuMa')
      } else if (response.code === '0') {
        setToken(response.data)
        next(false)
      } else {
        next(true)
      }
    }).catch(() => {
      next(true) // ajax出现错误
    })
  }
})

// 检测地址是否为微信分享地址
function isShareUrl () {
  let location = window.location
  let search = location.search
  if (search) {
    search = search.substr(1)
  }
  let href = ''
  let pathName = location.pathname
  if (pathName) {
    href += pathName
  }
  let searchArr = search.split('&')
  let searchRes = searchArr.filter(item => {
    if (item.indexOf('from=singlemessage') !== -1 || item.indexOf('from=groupmessage') !== -1 || item.indexOf('from=timeline') !== -1) {
      return true
    }
    return false
  })
  if (searchRes.length === 0) {
    // 如果不是微信分享则直接返回
    return false
  }
  searchArr = searchArr.filter(item => {
    if (item.indexOf('from=singlemessage') !== -1 || item.indexOf('from=groupmessage') !== -1 || item.indexOf('from=timeline') !== -1) {
      return false
    }
    if (item.indexOf('isappinstalled=') !== -1) {
      return false
    }
    return true
  })
  if (searchArr.length > 0) {
    href += '?' + searchArr.join('&')
  }
  setShareUrl(href)
}



