const weChatStatus = {
  state: {
    weChatLoginStatus: 0
  },

  mutations: {
    SET_WC_LOGIN_STATUS: (state, val) => {
      state.weChatLoginStatus = val
    }
  },

  actions: {
    setWeChatLoginStatus ({ commit }, val) {
      commit('SET_WC_LOGIN_STATUS', val)
    }
  }
}

export default weChatStatus



