<template>
  <van-tabbar class="page-bottom-menu" v-model="active" active-color="#22ac38">
    <van-tabbar-item
      v-for="(item, index) in menuList"
      :key="index"
      @click.stop="toPath(item.path)">
        {{item.name}}
      <div slot="icon" slot-scope="slotData">
        <img class="menu-img" :src="slotData.active ? item.iconActive : item.icon"/>
      </div>
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
export default {
  name: 'PageBottomMenu',
  components: {},
  props: {
  },
  data () {
    return {
      active: 0,
      menuList: [
        { id: 1, name: '首页', icon: '/static/images/commonicon/syxzw.png', iconActive: '/static/images/commonicon/syxz.png', path: '/' },
        { id: 2, name: '分类', icon: '/static/images/commonicon/sywxz.png', iconActive: '/static/images/commonicon/flwxz.png', path: '/category' },
        { id: 3, name: '推广', icon: '/static/images/commonicon/tgwxz.png', iconActive: '/static/images/commonicon/tgxz.png', path: '/popularize' },
        { id: 4, name: '菜篮子', icon: '/static/images/commonicon/clzwxz.png', iconActive: '/static/images/commonicon/clzxz.png', path: '/shoppingcart' },
        { id: 5, name: '个人', icon: '/static/images/commonicon/grwxz.png', iconActive: '/static/images/commonicon/grxz.png', path: '/personalcenter' }
      ]
    }
  },
  computed: {},
  created () {},
  methods: {
    toPath (path) {
      this.$router.push({ path: path })
    }
  },
  watch: {
    '$route': {
      handler (val) {
        this.active = -1
        for (let i = 0; i < this.menuList.length; i++) {
          if (val.path === this.menuList[i].path) {
            this.active = i
            break
          }
        }
      },
      immediate: true
    }
  }
}
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  .page-bottom-menu {
    padding: 0 0px 0 0px;
    height:95px;
    box-shadow: 0px 0px 3px 1px
    rgba(0, 0, 0, 0.28);
    .menu-img{
      width: 46px;height: 46px;
    }
    /deep/ .van-tabbar-item__text{
      font-size: 20px!important;
    }
  }
</style>



