import globalParam from '@/globalParam.js'
const baseUrl = globalParam.baseUrl
export default {
  /** ************* 3.商品信息模块**********************/
  searchlist: baseUrl + '/api/Goods/GetSearchGoodsList', // 商品搜索列表
  shoplist: baseUrl + '/api/Goods/GetGoodsList', // 商品列表(支持首页)
  mealList: baseUrl + '/api/Goods/GetSetMealList', // 套餐列表 (支持首页套餐列表)
  goodsinfodetail: baseUrl + '/api/Goods/GetGoodsInfoDetail', // 商品详情信息
  shopclasslist: baseUrl + '/api/Goods/GetGoodsClassList', // 商品分类列表
  mealListbyid: baseUrl + '/api/Goods/GetGoodsListByClass', //  分类Id获取套餐列表
  /** ***************** 4.公共信息模块************************************/
  swiperinfo: baseUrl + '/api/slideshow_info/GetSlideshowInfo'
}



