export default [
  {
    path: '/test',
    name: 'test',
    component: () => import('@/views/test')
  },
  {
    path: '/test1',
    name: 'test1',
    meta: {
      keepAlive: true
    },
    component: () => import('@/views/test/index1')
  },
  {
    path: '/test4',
    name: 'test4',
    component: () => import('@/views/test/index4'),
    meta: { showBottomMenu: true }
  },
  {
    path: '/test3',
    name: 'test3',
    component: () => import('@/views/test/index3.vue')

  },
  {
    path: '/login',
    name: 'login',
    component: () => import('@/views/login')
  },
  /** ********************** 首页**********************************/
  {
    path: '/',
    name: 'home',
    meta: { showBottomMenu: true },
    component: () => import('@/views/home/index.vue')
  },
  // 消息中心
  {
    path: '/messagecenter',
    name: 'messagecenter',
    component: () => import('@/views/home/messagecenter')
  },
  /** ********************** 分类** **********************/
  {
    path: '/category',
    name: 'category',
    meta: { showBottomMenu: true },
    component: () => import('@/views/category')
  },
  // 商品详情
  {
    path: '/category/productdetail',
    name: 'productdetail',
    component: () => import('@/views/category/productdetail/index')
  },
  /** ***************************** 推广****************************/
  {
    path: '/popularize',
    name: 'popularize',
    meta: { showBottomMenu: true },
    component: () => import('@/views/popularize')
  },
  /** ***************************购物车*******************************/
  {
    path: '/shoppingcart',
    name: 'shoppingCart',
    meta: { showBottomMenu: true },
    component: () => import('@/views/shopping-cart')
  },
  /** **************************个人中心*******************************/
  {
    path: '/personalcenter',
    name: 'personalCenter',
    meta: { showBottomMenu: true },
    component: () => import('@/views/personal-center')
  },
  /** 个人信息 ****/
  // 修改个人中心
  {
    path: '/personalcenter/updPersonalData',
    name: 'personalCnterUpdPersonalData',
    meta: { showBottomMenu: true },
    component: () => import('@/views/personal-center/updPersonalData')
  },
  // 填写个人中心
  {
    path: '/personalcenter/updPersonalData/addPersonalData',
    name: 'personalCnterAddPersonalData',
    component: () => import('@/views/personal-center/updPersonalData/addPersonalData')
  },
  /** 账户金额 ****/
  // 购卡记录
  {
    path: '/accountbalance',
    name: 'accountBalance',
    component: () => import('@/views/personal-center/account-balance')
  },
  /** 充值中心****/
  // 充值中心首页
  {
    path: '/vouchercenter',
    name: 'vouchercenter',
    component: () => import('@/views/personal-center/voucher-center')
  },
  // 我的收益
  {
    path: '/myearnings',
    name: 'myearnings',
    component: () => import('@/views/personal-center/myearnings')
  },
  /** 我的地址****/
  // 我的地址首页
  {
    path: '/myadress',
    name: 'myadress',
    component: () => import('@/views/personal-center/myadress')
  },
  // 新增地址
  {
    path: '/myadress/add',
    name: 'myadressadd',
    component: () => import('@/views/personal-center/myadress/add')
  },
  /** 搜索 ****/
  {
    path: '/search',
    name: 'search',
    component: () => import('@/views/search')
  },
  /** 自动配送管理 **/
  // 自动配送列表
  {
    path: '/autodistribution/list',
    name: 'autodistributionlist',
    component: () => import('@/views/auto-distribution/list')
  },
  // 自动配送设置
  {
    path: '/autodistribution/set',
    name: 'autodistributionset',
    component: () => import('@/views/auto-distribution/set')
  },
  // 自动配送详情
  {
    path: '/autodistribution/detail',
    name: 'autodistributiondetail',
    component: () => import('@/views/auto-distribution/detail')
  },
  /** 评价中心 **/
  // 评价列表页面
  {
    path: '/evaluate/list',
    name: 'evaluatelist',
    component: () => import('@/views/personal-center/evaluate/list')
  },
  // 评价页面
  {
    path: '/evaluate/submit',
    name: 'evaluatesubmit',
    component: () => import('@/views/personal-center/evaluate/submit')
  },
  // 评价详情
  {
    path: '/evaluate/detail',
    name: 'evaluatedetail',
    component: () => import('@/views/personal-center/evaluate/detail')
  },
  /** 退款**/
  // 退款申请
  {
    path: '/refund/application',
    name: 'refundapplication',
    component: () => import('@/views/personal-center/refund/application')
  },

  // 退款详情
  {
    path: '/refund/detail',
    name: 'refunddetail',
    component: () => import('@/views/personal-center/refund/detail')
  },
  // 我的退款（退款列表）
  {
    path: '/refund/list',
    name: 'refundlist',
    component: () => import('@/views/personal-center/refund/list')

  },
  /** 订单 **/
  // 订单详情
  {
    path: '/order/detail',
    name: 'orderdetail',
    component: () => import('@/views/personal-center/order/detail')
  },
  // 我的订单
  {
    path: '/order/myorder',
    name: 'myorder',
    component: () => import('@/views/personal-center/order/myorder')
  },
  // 确认订单
  {
    path: '/order/makesureorder',
    name: 'makesureorder',
    component: () => import('@/views/personal-center/order/makesureorder.vue')

  }
]



